@import '../../../styles/refs.scss';

.SourcePanel {
  &__wrap {
    padding: 0;
    padding-top: 40px;
    margin: 0;
    margin-bottom: 20px;
    list-style: none;
  }
  &__link {
    display: inline-flex;
    align-items: center;
    fill: $linkColor;
    color: $linkColor;

    &:hover {
      fill: $activeLinkColor;
      cursor: pointer;

      & > span {
        color: $activeLinkColor;
      }
    }
  }
  &__svgSource {
    margin-right: 10px;
  }
  &__linkAnchor {
    padding-top: 0px;
  }
}
